/*
Combination of http://meyerweb.com/eric/tools/css/reset/, http://github.com/necolas/normalize.css, http://ru.html5boilerplate.com and personal experience
by Robert Haritonov http://rhr.me/rreset
*/
html,
body,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
img,
ins,
kbd,
q,
samp,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
canvas,
embed,
figcaption,
hgroup,
output,
ruby,
summary,
time,
mark,
audio,
video,
button {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}
/*
* Prevents iOS text size adjust after orientation change, without disabling user zoom
* www.456bereastreet.com/archive/201012/controlling_text_size_in_safari_for_ios_without_disabling_user_zoom/
*/
html {
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
}
html,
body {
    width: 100%;
    height: 100%;
}
body {
    line-height: 1;
}
ul {
    list-style: none;
}
/*
* Improves readability when focused and also mouse hovered in all browsers
* people.opera.com/patrickl/experiments/keyboard/test
*/
a:hover,
a:active,
:focus {
    outline: none;
}
blockquote,
q {
    quotes: none;
    margin: 1em 40px;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}
/*
* Addresses styling not present in 8/9, S5, Chrome
*/
abbr[title] {
    border-bottom: 1px dotted;
}
/*
* Addresses style set to 'bolder' in FF3/4, S4/5, Chrome
*/
b,
strong {
    font-weight: normal;
}
/*
* Addresses styling not present in S5, Chrome
*/
dfn {
    font-style: italic;
}
mark {
    background: #ff0;
    color: #000;
}
/*
* Corrects font family set oddly in S5, Chrome
* en.wikipedia.org/wiki/User:Davidgothberg/Test59
*/
table {
    border-spacing: 0;
}
/*
* Corrects inline-block display not defined in 8/9 & FF3
*/
audio[controls],
canvas,
video {
    display: inline-block;
}
/*
* Improves readability of pre-formatted text in all browsers
*/
pre {
    white-space: pre;
    white-space: pre-wrap;
    word-wrap: break-word;
}
/*
* Prevents sub and sup affecting line-height in all browsers
* gist.github.com/413930
*/
sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}
/*
* Corrects overflow displayed oddly in IE9
*/
svg:not(:root) {
    overflow: hidden;
}
/*
* Addresses margin not present in 8/9, S5, O11
*/
figure {
    margin: 0;
}
/*
* Define consistent margin and padding
*/
fieldset {
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}
/*
* 1. Corrects color not being inherited in IE8/9
*/
legend {
    border: 0;
    /* 1 */
}
/*
* 1. Corrects font size not being inherited in all browsers
* 2. Addresses margins set differently in F3/4, S5, Chrome
* 3. Improves appearance and consistency in all browsers
*/
button,
input,
select,
textarea {
    font-size: 100%;
    /* 1 */

    margin: 0;
    /* 2 */

    vertical-align: baseline;
    /* 3 */
}
/*
* 1. Addresses FF3/4 setting line-height using !important in the UA stylesheet
*/
button,
input {
    line-height: normal;
    /* 1 */
}
/*
* 1. Improves usability and consistency of cursor style between image-type 'input' and others
* 2. Corrects inability to style clickable 'input' types in iOS
*/
button,
input[type='button'],
input[type='reset'],
input[type='submit'] {
    cursor: pointer;
    /* 1 */

    -webkit-appearance: button;
    /* 2 */
}
/*
* 1. Addresses appearance set to searchfield in S5, Chrome
* 2. Addresses box sizing set to border-box in S5, Chrome (include -moz to future-proof)
*/
input[type='search'] {
    -webkit-appearance: textfield;
    /* 1 */

    -moz-box-sizing: content-box;
    -webkit-box-sizing: content-box;
    /* 2 */

    box-sizing: content-box;
}
/*
* Corrects inner padding displayed oddly in S5, Chrome on OSX
*/
input[type='search']::-webkit-search-decoration {
    -webkit-appearance: none;
}
/*
* Corrects inner padding and border displayed oddly in FF3/4
* www.sitepen.com/blog/2008/05/14/the-devils-in-the-details-fixing-dojos-toolbar-buttons/
*/
button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}
/*
* 1. Removes default vertical scrollbar in 8/9
* 2. Improves readability and aligment in all browsers
*/
textarea {
    overflow: auto;
    /* 1 */

    vertical-align: top;
    /* 2 */
}
/* Remove X button on certain inputs in IE 10/11 */
::-ms-clear {
    display: none;
}

/* Remove "eye" icon on inputs type password in IE 10/11 */
::-ms-reveal {
    display: none;
}

/* Remove outline on radio button */
input[type='radio'] {
    outline: none;
}

input {
    padding: 0;
    border: 0;

    &:focus {
        box-shadow: none;
    }
}

a {
    text-decoration: none;
}

i {
    font-style: normal;
}

/* Hide native select element */

select {
    display: none;
}
